<template>
  <div class="login-page full-all" :style="{backgroundImage: `url(${loginBg})`}">
    <div class="login-frame">
      <div class="login-frame-content full-all">
        <div class="login-frame-title" :style="{backgroundImage: `url(${loginLogo})`}"></div>
        <el-form class="login-frame-form" :model="ruleForm" :rules="rules" ref="ruleForm" @submit.native.prevent>
          <el-form-item class="login-frame-form-item" prop="userName">
            <el-input v-model="ruleForm.userName" type="text" auto-complete="off" placeholder="账号">
<!--              <svg-icon slot="prefix" icon-class="user" class="el-input__icon input-icon" />-->
              <AntIcon slot="prefix" icon-class="UserOutlined" class="el-input__icon input-icon" />
            </el-input>
          </el-form-item>
          <el-form-item class="login-frame-form-item" prop="password">
            <el-badge :hidden="!isCapsLock" value="大写锁定开启" type="info">
              <el-input v-model="ruleForm.password" type="password" auto-complete="off" placeholder="密码" @blur="pwdBlur">
<!--                <svg-icon slot="prefix" icon-class="password" class="el-input__icon input-icon" />-->
                <AntIcon slot="prefix" icon-class="LockOutlined" class="el-input__icon input-icon" />
              </el-input>
            </el-badge>
          </el-form-item>
          <el-form-item class="login-frame-form-item" prop="code">
            <el-input v-model="ruleForm.code" type="text" auto-complete="off" placeholder="验证码" style="width: calc(100% - 135px);">
<!--              <svg-icon slot="prefix" icon-class="validCode" class="el-input__icon input-icon" />-->
              <AntIcon slot="prefix" icon-class="SafetyCertificateOutlined" class="el-input__icon input-icon" />
            </el-input>
            <div class="login-frame-form-item-img">
              <img :src="imgCode" @click="getVCode" />
            </div>
          </el-form-item>
          <el-form-item class="login-frame-form-item" style="opacity: 0;">
            <el-checkbox v-model="rememberMe">记住密码</el-checkbox>
          </el-form-item>
          <el-form-item prop="UUID" v-show="false">
            <el-input type="hidden" v-model="ruleForm.UUID" required></el-input>
          </el-form-item>
          <el-form-item class="login-frame-form-item">
            <el-button type="primary" @click="submitForm" v-if="!isLoging">登录</el-button>
            <el-button type="primary" v-if="isLoging" disabled>正在登陆...</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import loginBg from '@/assets/images/loginBg.png'
import loginLogo from '@/assets/images/loginLogo.png'
export default {
  name: 'Login',
  data () {
    // 自定义校验验证码
    let checkCode = (rule, value, callback) => {
      // if (process.env.NODE_ENV === 'development') {
      //   callback()
      //   return
      // }
      if (value.toLowerCase() === this.valieDateCode.toLowerCase()) {
        callback()
      } else {
        callback(new Error('验证码输入错误'))
      }
    }
    let imgCode = ''
    // if (process.env.NODE_ENV === 'development') {
    //   imgCode = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAHMAAAAcCAIAAADdgxc+AAABVklEQVR42u3ZsQ0CMQwAwJ8BIYHo' +
    //     '2IM5YA5mYAQmoKJA0FMwAzUdK9DQPpEivV5J3omdD7HBlotXoqc4RXnbNK1GmWiUQGVVVkNlBcie' +
    //     'blsnsb9wvO9Mol557A9dpr+1em/6yVrWZyXgomT7pihfh7WobzMWa3QxyoqVhVcA1uhifVmALx23' +
    //     'Y02UHUKEcQFBYbLRXcf0O7LArqTaIFEWe88Wkh0dt7Is4Qv247Kv+dJkpmxmbWCfz7OnSbtin4Mp' +
    //     '7MwCvtjCC1vPYkuu6BeM423g+/KsZ+GqS8A9W7pTCCJicR1QAbKlu1tAkIYrozYgsybKXqdra2ce' +
    //     'UGVDYtfLVDaHFXtmrXI/c2RZ37OZrARZ7C5gx7G7JVcCtWTFzA3GYiXUBsFZF3wVBAWZThGHJrOE' +
    //     'KW3F+Wyh5p4ue1lMaskSejC46mJ3Zn1rP9t/Df2HUWVVVsPGB5pP6N9kUMi2AAAAAElFTkSuQmCC'
    // }
    return {
      valieDateCode: null,
      imgCode: imgCode,
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // 表单数据
      ruleForm: {
        // 用户名
        userName: null,
        // 密码
        password: null,
        // 验证码
        code: null,
        // 唯一标识
        UUID: null
      },
      // 校验规则
      rules: {
        userName: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        code: [
          {required: true, message: '请输入验证码', trigger: 'blur'},
          {validator: checkCode, trigger: 'blur'}
        ]
      },
      isLoging: false,
      // 是否记住密码
      rememberMe: false,
      // 大小写控制
      isCapsLock: false,
      isCapsLockFirst: true,
      // 背景图片
      loginBg,
      // 标题 logo
      loginLogo
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.getVCode()
      // 添加键盘监听事件
      document.addEventListener('keydown', vm.keydownFun)
    })
  },
  beforeRouteLeave (to, from, next) {
    // 删除键盘事件
    document.removeEventListener('keydown', this.keydownFun)
    next()
  },
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    // 获取验证码
    getVCode () {
      // if (process.env.NODE_ENV === 'development') {
      //   return
      // }
      let param = null
      if (sessionStorage.getItem('UUID') !== null && sessionStorage.getItem('UUID') !== undefined && sessionStorage.getItem('UUID') !== 'null') {
        param = 'uuid=' + sessionStorage.getItem('UUID')
      }
      this.$api.getVerification(param).then((respone) => {
        if (respone.data.code === 0) {
          this.ruleForm.UUID = respone.data.data.uuid
          sessionStorage.setItem('UUID', respone.data.data.uuid)
          this.imgCode = respone.data.data.picBase64
          this.valieDateCode = respone.data.data.code
        }
      }).catch((response) => {
        console.log(response)
      })
    },
    /**
     * @desc 登录
     */
    loginOn () {
      // if (process.env.NODE_ENV === 'development') {
      //   // 保存 token
      //   sessionStorage.setItem('token', 'admin')
      //   // 进入主页
      //   this.$common.urlPushToMain()
      //   return
      // }
      this.isLoging = true
      // 登录请求
      let login = {
        userName: this.encryptByDES(this.ruleForm.userName),
        password: this.encryptByDES(this.ruleForm.password),
        code: this.ruleForm.code,
        uuid: this.encryptByDES(this.ruleForm.UUID)
      }
      this.$api.loginIn(login).then((respone) => {
        if (respone.data.code === 0) {
          // 保存 token
          sessionStorage.setItem('token', respone.data.data)
          // 进入主页
          this.$common.urlPushToMain()
        } else {
          this.$message.error(respone.data.message)
          this.ruleForm.password = null
          this.getVCode()
        }
        this.isLoging = false
      }).catch((error) => {
        console.log(`%c${error} 登陆失败！！`, 'color:blue')
        this.getVCode()
        this.isLoging = false
      })
    },
    /**
     * @desc 校验
     */
    submitForm () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.loginOn()
        } else {
          return false
        }
      })
    },
    /**
     * @desc 加密
     */
    encryptByDES (mes) {
      let keyHex = this.$cryptos.enc.Utf8.parse(this.ruleForm.code + this.ruleForm.code)
      let encrypted = this.$cryptos.DES.encrypt(mes, keyHex, {
        mode: this.$cryptos.mode.ECB,
        padding: this.$cryptos.pad.Pkcs7
      })
      return encrypted.toString()
    },
    // keydownFun: function (e) {
    //   let keyValue = e.keyCode ? e.keyCode : e.which
    //   if (keyValue === 13 && !this.isLoging) {
    //     this.submitForm()
    //   }
    // },
    pwdBlur () {
      this.isCapsLockFirst = true
      this.isCapsLock = false
    },
    /**
     * 键盘事件监听
     */
    keydownFun: function (e) {
      let keyValue = e.keyCode ? e.keyCode : e.which
      if (keyValue === 13 && !this.isLoging) {
        this.submitForm()
      } else if (((keyValue >= 65 && keyValue <= 90) && ((e.shiftKey && /[a-z]/.test(e.key)) || (!e.shiftKey && /[A-Z]/.test(e.key)))) || (keyValue === 20 && !this.isCapsLock && !this.isCapsLockFirst)) {
        this.isCapsLockFirst = false
        // 大写锁定提示
        this.isCapsLock = true
      } else if (keyValue === 20 && this.isCapsLock && !this.isCapsLockFirst) {
        this.isCapsLock = false
      }
    }
  }
}
</script>

<style scoped>
</style>



